//colors variables
$primary-background: #fff;
$second-background: #F3F3F3;
$submit-button-color:#00AD47;
$slider-pagination-color:#DDD;
$footer-bc:#DDD;
//fonts colors
$primary-tc: #000;
$second-tc: #fff;
$link-tc: #1faa50;
$form-placeholder-tc: #8c8c8c;

//fonts size
$primary-text-size: #{20px}/#{40px};
$second-ts: #{16px}/#{24px};
$header-title-ts: #{60px}/#{38px};
$button-ts: #{20px}/#{18px};
$primary-title-ts: #{30px}/#{38px};
$second-title-ts: #{18px}/#{40px};
$links_box-ts: #{15px}/#{22px};


.clr_ln:after {
  content: '';
  clear: both;
  display: table;
  width: 100%;
}
.title-template{
    text-align: center;
    font: $primary-title-ts "NunitoLight",Arial,Helvetica,sans-serif;
}
a {
  outline-style: none;
  &:focus {
    outline-style: none;
  }
}

.header_slider-wrapper{
  height: 100%;
  .swiper-container {
    width: 100%;
    height: 800px;
    margin:0 auto;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }
  .swiper-pagination{
    position: absolute;
    text-align: center;
    transition: 0.3;
    z-index: 10;
  }
  .swiper-pagination-bullets{
    right: 55px;
    top: 50%;
    transform: translate3d(0,-50%,0);
  }
  .swiper-pagination-bullet{
    width: 8px;
    height: 8px;
    display: block;
    border-radius: 100%;
    background: $slider-pagination-color;
    cursor: pointer;
    margin: 20px auto;
  }
  .swiper-pagination-bullet-active{
    width: 12px;
    height: 12px;
  }
}

#logo{
  position: absolute;
  left: 35px;
  top: 33px;
  z-index: 20;
}


.form-box{
  position: absolute;
  top: 385px;
  left: 50%;
  right: 50%;
  z-index: 20;
  transform: translate(-50%,-50%);
  width: 560px;
  text-align: center;
  color: $second-tc;
  h1{
    margin: 0 0 40px 0;
    font: $header-title-ts "NunitoLight",Arial,Helvetica,sans-serif;
    font-weight: 300;
  }
  p{
    font: 20px/27px "Yantramanav Light",Arial,Helvetica,sans-serif;
    font-weight: 300;
    margin: 0 0 57px 0;
  }
}


.form-template{
    input[type="text"]{
      width: 310px !important;
      height: 47px;
      border: 0 solid;
      border-radius: 20px 0 0 20px;
      padding:15px 10px 17px 30px;
      font: 18px/18.08px "Yantramanav Light",Arial,Helvetica,sans-serif;
      font-weight: 300;
      color: $primary-tc;
      margin:0;
    }
    input[type="text"]::placeholder{
      color: $form-placeholder-tc;
    }
    input[type="text"]::-moz-placeholder{
      color: $form-placeholder-tc;
    }
    input[type="text"]::-webkit-input-placeholder{
      color: $form-placeholder-tc;
    }
    input[type="text"]:-ms-input-placeholder{
      color: $form-placeholder-tc;
    }
    input[type="text"]::-ms-input-placeholder{
      color: $form-placeholder-tc;
    }
    input[type="submit"]{
      width: 166px !important;
      height: 47px;
      border: 0 solid;
      border-radius: 0 20px 20px 0;
      padding: 15px 0 17px 0;
      font: 20px/18.08px "NunitoRegular",Arial,Helvetica,sans-serif;
      font-weight: 400;
      color: $second-tc;
      background: $submit-button-color;
    }
}
.content-about{
  padding:120px 127px 100px 112px;
  font: $second-ts "YantramanavLight",Arial,Helvetica,sans-serif;
  color: $primary-tc;
  h1{
    @extend .title-template;
    margin: 0 0 46px 0;
  }
  p{
    margin: 0 0 34px 0;
  }
}
.first-quote{
  font: 20px/30px "YantramanavThin",Arial,Helvetica,sans-serif;
  color: $primary-tc;
  text-align: center;
  padding: 195px 148px 184px 161px;
  h1{
    @extend .title-template;
    margin: 0 0 130px 0;
  }
  blockquote{
    position: relative;
    margin: 0 22px;
    q{
      display: inline-block;
      quotes: "\301E" "\301D";
      margin: 0 0 74px 0;
    }
    q:before,
    q:after{
      position: absolute;
      font-size: 60px;
      color: #ddd;
    }
    q:before{
      content: open-quote;
      left: -25px;
      top: -10px;
    }
    q:after{
      content: close-quote;
      right: -25px;
      bottom: 65px;
    }
    cite{
      display: block;
      font: 20px/30px "YantramanavThin",Arial,Helvetica,sans-serif;
      color: $primary-tc;
    }
  }
}
.second-quote{
  @extend .first-quote;
  padding: 320px 148px 184px 161px;
  blockquote{
    q:after{
      right: -35px;
      bottom: 35px;
    }
  }
}
.header_range{
  padding: 80px 0 0 0;
  h1{
    @extend .title-template;
    margin: 0 0 10px 0;
  }
  h2{
    font: $second-title-ts "YantramanavLight",Arial,Helvetica,sans-serif;
    color: $primary-tc;
    text-align: center;
    margin: 0 0 135px 0;
  }
}
.container_range{
  padding: 0 210px 0 210px;
  height: 100%;
  .swiper-container{
    width: 100%;
    height: 100%;
    margin: 0 auto 60px;
    position: relative;
    overflow: hidden;
    z-index: 1;
    .swiper-wrapper{
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 1;
      display: flex;
      transition-property: transform;
      box-sizing: content-box;
    }
    .swiper-slide{
      text-align: center;
      flex-shrink: 0;
      width: 100%;
      height: 100%;
      position: relative;
      display: inline-block;
      cursor: pointer;
    }
    .swiper-pagination{
      position: absolute;
      text-align: center;
      transition: 0.3;
      transform: translate3d(0,0,0);
      z-index: 10;
      font: $second-title-ts "YantramanavThin",Arial,Helvetica,sans-serif;
    }
    .swiper-pagination-fraction {
      bottom: 0;
      left: 0;
      width: 100%;
    }
    .swiper-button-prev,
    .swiper-button-next{
      position: absolute;
      top: 110px;
      z-index: 50;
      cursor: pointer;
      font-size: 8px;
      color: #cdcdcd;
      border: 2px solid #CDCDCD;
      border-radius: 20px;
      padding: 2px 14px 2px 13px;
      &:hover{
        color: #adadad;
        border-color: #adadad;
      }
    }
    .swiper-button-next{
      right: 0;
      padding: 2px 13px 2px 14px;
    }
    img{
      margin: 0 0 100px 0;
    }
    h3{
      font: 24px/38.4px "NunitoLight",Arial,Helvetica,sans-serif;
      color: $primary-tc;
      margin: 0 0 50px 0;
    }
  }
}
.header_links{
  @extend .header_range;
  h2{
    margin: 0 0 80px 0;
  }
}
.links_row{
  margin-bottom: 60px;
  &:last-child{
    margin-bottom: 90px;
  }
}
.links_box{
  margin-right: 5px;
  font: $links_box-ts "YantramanavThin",Arial,Helvetica,sans-serif;
  h3{
    margin: 0 0 10px 0;
    font: $second-ts "YantramanavRegular",Arial,Helvetica,sans-serif;
  }
  span{
    display: block;
    margin: 0 0 10px 0;
    &:last-child{
      margin: 0 0 18px 0;
    }
  }
  a{
    font: 15px/30px "YantramanavThin",Arial,Helvetica,sans-serif;
    color: $link-tc;
  }
}
.header_like-plae{
  color: $primary-tc;
  text-align: center;
  h1{
    font: 24px/38.4px "NunitoLight",Arial,Helvetica,sans-serif;
    margin: 0 0 10px 0;
  }
  h2{
    font: 18px/36px "YantramanavThin",Arial,Helvetica,sans-serif;
    margin: 0 0 30px 0;
  }
}
.contacts_row{
  margin: 0 0 75px 0;
}
.mail-plae_box{
  text-align: center;
  a{
    font: 18px/38.4px "YantramanavThin",Arial,Helvetica,sans-serif;
    color: $link-tc;
    text-decoration: none;
  }
}
.phone-number_box{
  color: $primary-tc;
  text-align: center;
  span{
    font: 18px/38.4px "YantramanavThin",Arial,Helvetica,sans-serif;
  }
}
.form-title{
  text-align: center;
  h1{
    font: 30px/36px "NunitoLight",Arial,Helvetica,sans-serif;
    color: $primary-tc;
    margin: 0 0 10px 0;
  }
  h2{
    font: 22px/38px "YantramanavThin",Arial,Helvetica,sans-serif;
    color: $primary-tc;
    margin: 0 0 47px 0;
  }
}
.form-container{
  background: #f3f3f3;
  padding: 125px 0 112px 0;
}
.block_form{
  text-align: center;
}
.copyright_box{
  background: $footer-bc;
  padding: 13px 0 13px 37px;
}
.copyright{
  font: 14px/20px "YantramanavRegular",Arial,Helvetica,sans-serif;
  color: $second-tc;
}