@font-face {
  font-family: 'NunitoLight';
  src: local('Nunito-Light'),
        url('../fonts/nunito-light.woff2') format('woff2'),
        url('../fonts/nunito-light.woff') format('woff'),
        url('../fonts/nunito-light.ttf') format('truetype');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'NunitoRegular';
  src: local('Nunito-Regular'),
        url('../fonts/nunito-regular.woff2') format('woff2'),
        url('../fonts/nunito-regular.woff') format('woff'),
        url('../fonts/nunito-regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'YantramanavLight';
  src: url('../fonts/Yantramanav-Light.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'YantramanavRegular';
  src: url('../fonts/Yantramanav-Regular.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: 'YantramanavThin';
  src: url('../fonts/Yantramanav-Thin.ttf') format('truetype');
  font-style: normal;
  font-weight: normal;
}
